.TextArea{
    display: flex;
    flex-direction: column;
    text-align: left;
    background-color: rgb(245, 246, 247);
    width: thin;
    width: 100%;
    height: 100%;
	text-align: justify;
}
.Image{
    width: 100%;
    height: auto;
    top: var(--topbarHeight);
}
.MainPresentation{
    border: 2px solid black;
    margin: 7%;
    padding: 15px;
}
.mediaDisplay{
    display: flex;
    justify-content: space-around;
}
.mediaDisplay iframe{
    width: 560px;
    height: 315px;
}
.TextArea h1{
	font-family: Impact, Charcoal, sans-serif;
}
